.about-container {
    overflow-x: hidden;
    font-size: 60px;
    margin: 0;
    width: 100%;
    z-index: 100;
    padding-top: 85px;
    font-family: 'Courier', serif;
    color: #f2f2f2;
    background-color: black;
}

.about-title {
    font-size: 50px;
    margin-bottom: 50px;
    z-index: 100;
    padding-top: 45px;
    font-family: Harriet, serif;
    text-align: center;
    font-weight: lighter;
    color: #f2f2f2;
}

.about-title:before,
.about-title:after {
 background-color: #bfbfbf;
 content: "";
 display: inline-block;
 height: 2px;
 position: relative;
 vertical-align: middle;
 width: 25%;
}

.about-title:before {
 right: 0.5em;
 margin-left: -50%;
}
.about-title:after {
 left: 0.5em;
 margin-right: -50%;
}

.about-container p {
    width: 55%;
    color: #f2f2f2;
    margin-top: 80px;
    font-size: 20px;
    padding: 10px 0;
}

.about-container p:first-letter {
  padding: 0 .3rem;
  margin: 0 .3rem 0 0;
  font-family: Harriet, serif;
  font-size: 100px;
  float: left;
  line-height: 1;

}

.skills-container {
    z-index: 100;
    display: flex;
    text-align: left;
    justify-content: space-around;
    font-family: Harriet, serif;
    flex-wrap: wrap;
    /* border-bottom: 3px solid #f2f2f2; */
    color: #f2f2f2;
}
