.about {
    display: flex;
    align-items: center;
    flex-direction: column;
}


.about p {
    color: #f2f2f2;
    font-family: Balto-Med, sans-serif;
    margin-top: 0px;
    line-height: 50px;
    font-size: 17px;
}

.about-button {
    text-decoration: none;
    font-family: Balto-Med, sans-serif;
    color: #f2f2f2;
    padding: 0px;
    transition: all .3s;
    font-size: 15px;
  }

  .skills-title {
    display: flex;
    width: 50%;
    margin: 0 auto;
    padding: 10px;
    font-size: 18px;
    margin-top: 40px;
    text-align: center;
    justify-content: space-evenly;
    color: #f2f2f2;
  }

  .app, .tech {
    align-items: center;
    font-size: 18px;
    cursor: pointer;
    display: flex;
    align-items: center;
    font-family: Balto-Med, sans-serif;
    color: #f2f2f2;
    transition: all .3s;
    padding: 5px;
    white-space: nowrap;
  }

.app {
}

.app::before,
.app::after {
  display: inline-block;
  opacity: 0;
  -webkit-transition: -webkit-transform 0.3s, opacity 0.2s;
  -moz-transition: -moz-transform 0.3s, opacity 0.2s;
  transition: transform 0.3s, opacity 0.2s;
}

.app::before {
  margin-right: 10px;
  content: '[';
  -webkit-transform: translateX(20px);
  -moz-transform: translateX(20px);
  transform: translateX(20px);
}

.app::after {
  margin-left: 10px;
  content: ']';
  -webkit-transform: translateX(-20px);
  -moz-transform: translateX(-20px);
  transform: translateX(-20px);
}

.app:hover::before,
.app:hover::after,
.app:focus::before,
.app:focus::after {
  opacity: 1;
  -webkit-transform: translateX(0px);
  -moz-transform: translateX(0px);
  transform: translateX(0px);
}

.tech::before,
.tech::after {
  display: inline-block;
  opacity: 0;
  -webkit-transition: -webkit-transform 0.3s, opacity 0.2s;
  -moz-transition: -moz-transform 0.3s, opacity 0.2s;
  transition: transform 0.3s, opacity 0.2s;
}

.tech::before {
  margin-right: 10px;
  content: '[';
  -webkit-transform: translateX(20px);
  -moz-transform: translateX(20px);
  transform: translateX(20px);
}

.tech::after {
  margin-left: 10px;
  content: ']';
  -webkit-transform: translateX(-20px);
  -moz-transform: translateX(-20px);
  transform: translateX(-20px);
}

.tech:hover::before,
.tech:hover::after,
.tech:focus::before,
.tech:focus::after {
  opacity: 1;
  -webkit-transform: translateX(0px);
  -moz-transform: translateX(0px);
  transform: translateX(0px);
}

