.skills {
    font-family: Balto-Med, sans-serif;
    height: 200px;
    font-size: 18px;
    color: #f2f2f2;
    line-height: 30px;
    display: flex;
    justify-content: center;
    list-style: none;
}

.skills ul {
    /*display: grid;
    grid-template-columns: 2fr 2fr;*/
    list-style: none;
    padding-left: 0;
    display: flex;
    justify-content: center;
    text-align: center;
}

.skills li {
    padding: 10px 25px;
    text-align: left;
}

i {
}

  .show {
    display: block;
    animation: fadeIn 600ms ease-in;
  }

  .remove {
    display: none;
  }


@keyframes fadeIn {
  0%   { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes fadeOut {
  0%   { opacity: 1; }
  100% { opacity: 0; }
}

.clicked {
    padding: 5px;
    font-size: 18px;

}

.clicked::before {
  margin-right: 10px;
  content: '[';
  -webkit-transform: translateX(20px);
  -moz-transform: translateX(20px);
  transform: translateX(20px);
}

.clicked::after {
  margin-left: 10px;
  content: ']';
  -webkit-transform: translateX(-20px);
  -moz-transform: translateX(-20px);
  transform: translateX(-20px);
}

.clicked:hover::before,
.clicked:hover::after,
.clicked:focus::before,
.clicked:focus::after {
  opacity: 1;
  -webkit-transform: translateX(0px);
  -moz-transform: translateX(0px);
  transform: translateX(0px);
}


@media screen and (max-width: 759px) {


    .skills-container {
        flex-direction: column;
        flex-wrap: wrap;
    }

    .about-container {
        bottom: 10px;
        background-color: rgba(0,0,0,0);
        padding-top: 140px;
        position: relative;
    }

    .about-title {
        padding-top: 0px;
        font-size: 45px;
        margin-bottom: 0;
    }

    .about p {
        font-size: 15px;
        line-height: 1.9;
        width: 80%;
    }

    .about-container p:first-letter {
        font-size: 56px;
    }

    .about-button {
        font-size: 15px;
    }

    .app, .tech {
        font-size: 14px;
        white-space: nowrap;
    }

    .skills li {
        padding: 5px 10px;
          float: left;
        text-align: left;
        position: relative;
        top: 30px;
    }

     .remove {
        display: block;
        opacity: 0;
    }

    .skills ul{
        font-size: 15px;
        position: relative;
        bottom: 75px;
        display: flex;
        padding: 0;
        margin: 0;
    }

    .skills-title {
        margin: 0 auto;
        position: relative;
        width: 100%;
        padding: 50px 0 20px 0;
        bottom: 50px;
    }

    .clicked {
      font-size: 14px;
    }

    .skills {
      width: 100%;
      display: flex;
      justify-content: space-evenly;
    }

    .techSkills, .appSkills {
      width: 90%;
      float: left;
    }

    .show ul .appSkills {
      padding-right: 50px
    }

}

