@font-face {
    font-family: Harriet;
    src: url(HarrietDisplay-Black.ttf);
}

.navHolder {
    font-family: Harriet, serif;
    position: fixed;
    top: 0px;
    font-size: 14px;
    padding-left: 0;
    display: flex;
    padding-right: 0;
    width: 100%;
    z-index: 1000;
    pointer-events: none;
}

.burger {
    pointer-events: all;
    display: none;
    font-size: 20px;
    width: 1%;
    position: fixed;
}

.navHolder ul {
    pointer-events: all;
    display: flex;
    background-color: black;
    align-items: center;
    list-style: none;
    justify-content: center;
    padding: 0;
    float: right;
    text-align: right;
    padding: 10px;
    width: 100%;
    text-align: left;
    margin: 0;
    position: relative;
}

.nav a {
    z-index: 2001;
    text-decoration: none;
    margin: 0 10px;
    display: flex;
    padding: 0px 5px;
    color: #f2f2f2;
    float: right;
    text-transform: capitalize;
    text-decoration: none;
}

.navHolder li a::before,
.navHolder li a::after {
  display: inline-block;
  opacity: 0;
  -webkit-transition: -webkit-transform 0.3s, opacity 0.2s;
  -moz-transition: -moz-transform 0.3s, opacity 0.2s;
  transition: transform 0.3s, opacity 0.2s;
}

.navHolder li a::before {
  margin-right: 10px;
  content: '[';
  -webkit-transform: translateX(20px);
  -moz-transform: translateX(20px);
  transform: translateX(20px);
}

.navHolder li a::after {
  margin-left: 10px;
  content: ']';
  -webkit-transform: translateX(-20px);
  -moz-transform: translateX(-20px);
  transform: translateX(-20px);
}

.navHolder li a:hover::before,
.navHolder li a:hover::after,
.navHolder li a:focus::before,
.navHolder li a:focus::after {
  opacity: 1;
  -webkit-transform: translateX(0px);
  -moz-transform: translateX(0px);
  transform: translateX(0px);
}

/*.nav a:hover, .nav a:focus {
  text-shadow: 0 0 0.5em white;
  color: white;
  filter: blur(0.007em);
}*/

.sub-name {
    font-family: Harriet, serif;
    color: #f2f2f2;
    margin: 0;
    display: flex;
    color: #f2f2f2;
    width: 100%;
    color: inherit;
    float: right;
}

.sub-name a {
    margin: 0;
}

.sub-name a:hover, .sub-name a:focus{
    background-size: 0% 0px;
    color: #f2f2f2;
}

.active {
  bottom: 0;
}

@media screen and (max-width: 759px) {
    .navHolder ul {
        padding-bottom: 37px;
        align-items: stretch;
    }


    ul {
        flex-direction: column;
        text-align: right;
        bottom: 142px;
        transition: 300ms ease-in-out bottom;
        width: 100%;
        padding-bottom: 37px;
    }

    li {
        padding: 10px;
    }

    .burger {
        top: 14px;
        display: block;
        cursor: pointer;
    }

    .sub-name {
        display: none;
    }
}





