.container {
    overflow: hidden;
    position: relative;
    color: #f2f2f2;
    display: flex;
    min-height: 100vh;
    font-family: Harriet, serif;
    align-items: center;
    flex-direction: column;
    justify-content: center;

}

h1 {
  position: relative;
  font-size: 80px;
  bottom: 0;
  right: 0;
  margin: 0;
  z-index: 100;
}

.name {
  transition: all 300ms ease;
}

