.list {
  display: flex;
  margin: 0;
  position: relative;
  width: 250px;
  letter-spacing: 1px;
  bottom: 18px;
  font-family: Balto-Med, sans-serif;
  transform: skewX(-30deg);
  flex-direction: column;
  left: 100px;
  z-index: 100;
}

.background {
  position: absolute;
  z-index: -100;
  min-width: 100%;
  min-height: 100%;
  filter: blur(5px);
  opacity: 0.3;
}

.site-title {
  font-size: 2rem;
}

.view {
  text-decoration: none;
  color: #f2f2f2;
  padding: 5px;
  /* background-image: linear-gradient(rgb(255, 238, 0), rgb(255, 238, 0)); */
  background-image: linear-gradient(#f2f2f2, #f2f2f2);
  background-position: 0% 100%;
  background-repeat: no-repeat;
  background-size: 0% 30px;
  transition: all .3s;
  font-family: Balto-Med, sans-serif;
}

.view::before,
.view::after {
  display: inline-block;
  opacity: 0;
  -webkit-transition: -webkit-transform 0.3s, opacity 0.2s;
  -moz-transition: -moz-transform 0.3s, opacity 0.2s;
  transition: transform 0.3s, opacity 0.2s;
}

.view::before {
  margin-right: 10px;
  content: '[';
  -webkit-transform: translateX(20px);
  -moz-transform: translateX(20px);
  transform: translateX(20px);
}

.view::after {
  margin-left: 10px;
  content: ']';
  -webkit-transform: translateX(-20px);
  -moz-transform: translateX(-20px);
  transform: translateX(-20px);
}

.view:hover::before,
.view:hover::after,
.view:focus::before,
.view:focus::after {
  opacity: 1;
  -webkit-transform: translateX(0px);
  -moz-transform: translateX(0px);
  transform: translateX(0px);
}

